@import '../untils/variables';

.recording-section {
  position: relative;
  width: 750px;
  align-self: center;
  background: $recording-lid-background-color;
  border: 1px solid $form-border-color;
  box-shadow: 0 0 30px $form-box-shadow-color;
  border-radius: 10px;

  &__header {
    position: relative;
    display: flex;
    align-items: center;
    padding: 20px;
    gap: 5px;
  }

  &__title {
    flex-grow: 2;
    padding: 10px 15px;
    font-size: 17px;
    font-weight: 700;
    line-height: 18px;
  }

  &__btn {
   display: flex;
    align-items: center;
  }

  &__info {
    position: absolute;
    z-index: 2;
    display: none;
    margin: -6px -1px 0;
    padding: 16px 0 0 0;
    background: $form-background-color;
    border: 1px solid $form-border-color;
    box-shadow: 0 0 30px $form-box-shadow-color;
    border-radius: 0 6px 6px 6px;

    &--show {
      display: flex;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        background: #fff;
      }
    }
  }
}
