@import '../untils/variables';

.modal {
  position: fixed;
  z-index: -100;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  &.is--visible {
    $modalVisible: & !global;
    z-index: 100;
  }

  &__blackout {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0;
    transition: opacity 50ms linear;

    &.is-blacked-out {
      opacity: 1;
    }
  }

  &__inner {
    position: relative;
    width: 570px;
    height: 345px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-self: center;
    text-align: center;
    margin: 0 auto;
    padding: 30px 30px 45px;
    border: 1px solid $modal-border;
    box-shadow: 0 5px 10px $modal-box-shadow-color;
    background: $modal-inner-background;
    transform: translateY(50%);
    opacity: 0;
    transition: opacity 100ms linear;


    #{$modalVisible} & {
      opacity: 1;
    }
  }

}
