@import '../untils/variables';

.form-checkbox {
  display: flex;
  align-items: center;
  margin-right: 30px;
  margin-bottom: 5px;
  color: $form-checkbox-color;
  font-size: 17px;
  line-height: 18px;

  &--ss {
    $checkboxSizeSmall: & !global;
  }

  &--pr {
    $checkboxPositionRight: & !global;
    margin-right: 0;
  }


  &__control {
    position: absolute;
    z-index: -1;
    opacity: 0;

    &:checked {
      $checkboxIsChecked: & !global;
    }
  }

  &__text {
    display: inline-flex;
    align-items: center;
    user-select: none;
    cursor: pointer;

    &:hover {
      color: $border-checkbox-color-hover;

      &::before {
        border-color: $border-checkbox-color-hover;
      }
    }

    &:active {
      &::before {
        border-color: $border-checkbox-color-active;
      }
    }

    #{$checkboxPositionRight} & {
      flex-grow: 1;
    }

    &::before {
      content: '';
      width: 20px;
      height: 20px;
      flex-shrink: 0;
      flex-grow: 0;
      margin-right: 8px;
      padding: 7px;
      border: 1px solid $border-radio-color;
      border-radius: 3px;

      #{$checkboxPositionRight} & {
        order: 1;
        margin-right: 0;
        margin-left: auto;
      }

      #{$checkboxSizeSmall} & {
        width: 18px;
        height: 18px;
      }
    }

    &::after {
      #{$checkboxIsChecked} + & {
        content: "";
        height: 12px;
        width: 6px;
        margin: -3px -13px 0 7px;
        transform: rotate(45deg);
        border-bottom: 3px solid $border-checkbox-color-active;
        border-right: 3px solid $border-checkbox-color-active;
        order: -1;
      }
    }
  }
}
