@import '../untils/variables';

.hidden-search {
  display: none !important;
}

.vsb-menu {
  cursor: pointer;
  z-index: 1000;
  display: block;
  visibility: hidden;
  position: absolute;
  left: 0;
  right: 0;
  margin-top: 10px;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid $form-input-border-color;
  box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
  border-radius: 6px;
  font-size: 11px;
}

.vsb-js-search-zone {
  position: relative;
  z-index: 1001;
  margin: 10px;

  .form-input {
    width: 100%;
  }
}

.vsb-main {
  position: relative; /*Don't change*/
  display: inline-block;
  width: 100%;
  vertical-align: middle;
  text-align: left;
}

.vsb-menu li {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 12px;
  background-color: #fff;
  padding: 0.2em;

  &:hover {
    color: $border-checkbox-color-hover;
  }
}


.vsb-menu ul {
  user-select: none;
  list-style: none;
  white-space: nowrap;
  margin: 4px 0 0 0;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 3px;
  color: #333;
  cursor: pointer;
  overflow-y: auto;
}

li.disabled {
  cursor: not-allowed;
  opacity: 0.3;
  background-color: #999;
}

.vsb-main button {
  display: flex;
  min-width: 120px;
  min-height: 34px;
  font-family: $general-font;
  border-radius: 6px;
  width: 100%;
  text-align: left;
  z-index: 1;
  color: #333;
  background: white;
  border: 1px solid $form-input-border-color;
  line-height: 20px;
  font-size: 15px;
  padding: 6px 10px;


  &[readonly] {
    background: $form-input-background-readonly;
  }

  &:hover {
    border-color: $form-input-border-color-hover;

    &[readonly] {
      background: $form-input-background-readonly-hover;
      border-color: $form-input-border-color;
    }
  }

  &:focus {
    border-color: $form-input-border-color-focus;
    box-shadow: 0 0 0 2px $form-input-outline-color-focus;
    outline: none;

    &[readonly] {
      background: $form-input-background-readonly-focus;
      border-color: $form-input-border-color;
      box-shadow: none;
    }
  }
}

.vsb-main button.disabled {
  cursor: not-allowed;
  opacity: 0.65;
}

.vsb-main .title {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-right: 20px;
  user-select: none;
}

.vsb-main ul {
  white-space: nowrap;
}

.vsb-menu li {
  font-size: 14px;
  background-color: #fff;
  min-height: 1.4em;
  padding: 0.2em 2em 0.2em 1em;
}

.vsb-menu li.grouped-option {
  font-weight: bold;
  font-size: 15px;
  padding-left: 5px;
}

.vsb-menu :not(.multi) li.active::before {
  content: "";
  display: inline-block;
  font-size: inherit;
  float: left;
  margin-left: -13px;
  transform: rotate(45deg);
  height: 12px;
  width: 7px;
  border-bottom: 3px solid #000;
  border-right: 3px solid #000;
  border-radius: 2px;
}

.vsb-menu .multi li {
  font-size: 14px;
  background-color: #fff;
  min-height: 1.4em;
  padding: 0.2em 2em 0.2em 26px;
}

.vsb-menu .multi li.grouped-option {
  font-weight: bold;
  font-size: 15px;
  padding-left: 5px;
}

.vsb-menu .multi li:not(.grouped-option) {
  &::before {
    content: "";
    margin-left: -25px;
    border: 1px solid $border-radio-color;
    border-radius: 3px;
    padding: 7px;
    margin-top: 0;
  }

  &:hover::before {
    border-color: $border-checkbox-color-hover;
  }
}

.vsb-menu .multi li.active::after {
  content: "";
  height: 12px;
  width: 6px;
  margin: -3px 9px 0 -20px;
  transform: rotate(45deg);
  border-bottom: 3px solid #000;
  border-right: 3px solid #000;
  order: -1;
}

.caret {
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 7px 5px 0;
  border-color: $form-select-border-color-arrow transparent transparent transparent;
  margin-left: -21px;
  margin-right: 6px;
  pointer-events: none;
}
